import React, { useContext } from 'react';
import { AppContext } from '../../App';
import 'chart.js/auto';
import { Chart } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';

import CompLoader from '../compLoader/CompLoader';
import CompLi from './CompLi';

const CompHourly = () => {
    const { _weatherData } = useContext(AppContext)
    let hourlyData
    let chartArr//chartdar
    let chartData //chart  data 
    let chartOptions//chart  option 
    if (_weatherData) {//chart JS
        hourlyData = _weatherData.hourly

        //chartJs  
        chartArr = hourlyData.map(v => parseFloat((v.temp - 273.15).toFixed(1)))//chart JS 

        //chart Js 
        chartData = {
            labels: new Array(48).fill(''),
            datasets: [{
                label: 'myGraph',
                data: chartArr, //
                borderWidth: 1,
                fill: false,
                borderColor: 'rgb(255, 255, 255)',
                tension: 0.5,
            }]
        }

        //chartJS 
        chartOptions = {
            scales: {
                x: {
                    display: false,
                },
                y: {
                    display: true,
                    beginAtZero: true,
                    min: -50,
                    max: 50,
                    stepSize: 10,
                    border: {
                        display: false,
                        color: '#8#ed1f1', //
                        width: 1, //
                        dash: [2, 2],//

                    },
                    ticks: {//
                        display: true,
                        color: '#8ed1f1',
                        stepSize: 20,
                    },
                    grid: {
                        color: '#8ed1f1' //
                    },
                },
            },
            plugins: {
                legend: {
                    display: false,//
                    position: 'top',
                    labels: {
                        color: '#FFF'
                    }
                },//legend
                datalabels: {//  
                    display: true,
                    color: "#4378a3",
                    align: 'top',
                },
            },//plguins
            responsive: true,
            maintainAspectRatio: false,
        }
        
    }
    return (
        <section className="comp-hourly">
            <div className='section-inner' >
                <h2>hourly</h2>
                {
                    (_weatherData)
                        ?
                        <div className="wrap">
                            <ul>
                                {
                                    hourlyData.map(v => <CompLi key={v.dt} data={v} />)
                                }
                            </ul>
                            <div className='chart-wrap'>
                                <Chart type='line' data={chartData} options={chartOptions} plugins={[ChartDataLabels]} />
                            </div>
                        </div>
                        :
                        <CompLoader />
                }
            </div>
        </section>
    );
};

export default CompHourly;