import React, { useContext, useEffect } from 'react';
import { AppContext } from '../../App';
import { fnSetWeatherInfo } from '../../js/weather';
import CompSvg from './CompSvg';

const CompCurrentOutput = () => {

    const { _weatherData, fnAppInit, _timezone, _addressKo, _addressEn } = useContext(AppContext)
    //_watherData ()       .

    //_weatherData _address(fnSetWeatherInfo  )    compOutput   
    const {
        temp, icon, bg, desc, windDeg,
        windSpeed, humidity, rain, uv, uviDesc,
        sunrise, sunset, currentTime, dt, day, apm, snow
    } = fnSetWeatherInfo(_weatherData.current, _weatherData.timezone)
    
    const fnResetHandler = () => {
        let result = window.confirm('    ?')
        if(result) fnAppInit()
    }
    
    
    return (
        <div className='current-output' style={{ backgroundImage: `url(${process.env.PUBLIC_URL}/img/weather/${bg}.jpg)` }}>

            <button className='reload' onClick={fnResetHandler}><i className="fa-solid fa-arrows-rotate"></i></button>

            <address>
                <span>{_addressKo} {_addressEn}</span>
                <span>{_addressKo} {_addressEn}</span>
            </address>

            <div className='circle-container'>

                <div className="sunset">
                    <CompSvg currentData={_weatherData.current} />
                    
                    <time className='sunrise-time'>
                    {sunrise}
                    </time>
                    <time className='sunset-time'>
                    {sunset}
                    </time>
                </div>
                <div className="circle-container-inner">
                    <p className='temp'>
                        <em>{temp}<sup>&#176;c</sup></em>
                        {/* sup  */}
                        <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
                    <span>
                        
                        <i className="fa-solid fa-face-smile"></i>
                        <b>23<sup>&#176;c</sup></b>
                    </span>
                    </p>
                        
                    <p className='desc'>{desc}</p>
                    <p className='etc'>
                        <span className='wind'>
                            <i style={{ 'transform': `rotate(${windDeg - 45}deg)` }} className="fa-solid fa-location-arrow"></i>
                            <i className="fa-solid fa-wind"></i>
                            <b>{windSpeed}</b>
                            <small>m/s</small>

                        </span>

                        <span className='humidity'>
                            <i className="fa-solid fa-droplet"></i>
                            <b>{humidity}</b>
                            <small>%</small>
                        </span>
                        <span className='uvi'>
                            <i className="fa-solid fa-sun"></i>
                            <b>{uv}</b>
                            <small>{uviDesc}</small>
                        </span>
                        {
                    
                         (rain !==0)&&
                        <span className='rain'>
                            <i className="fa-solid fa-umbrella"></i>
                            <b>{rain}</b>
                            <small>mm</small>
                        </span>
                        }
                        {
                        //(&&) strict  
                        //(rain)&& ...  false (0 )
                        //(rain!==0)&& <-  undefind 
                         (snow !==0)&&
                        <span className='snow'>
                            <i className="fa-solid fa-snowflake"></i>
                            <b>{snow}</b>
                            <small>mm</small>
                        </span>
                        }
                    </p>
                    <p className='time-date'>
                        <time className='time'><small>{apm}</small> {currentTime}</time>
                        <time className='date'>{dt} {day} </time>
                    </p>
                </div>
            </div>{/* circle-container */}
        </div>
    );
};

export default CompCurrentOutput;