export const fnGetLatLon = () => {//  
    return new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(
            (location) => {//success
                resolve(
                    {
                        lat: location.coords.latitude, //
                        lon: location.coords.longitude //
                    })

            },

            (error) => {//failed
                if (error.code === 1) alert('   .')
                else alert('     .')
                reject(new Error('    .'))
                //= throw new Error(' .    .')
            })//getCurrentPosition
    })//Promise
}//fnGetLatLon

