/*      */
export const fnGetAddress = (latLonObj, lang) => {

    return new Promise((resolve) => {
        let geocoder = new window.google.maps.Geocoder;
        let address
        let { lat, lon } = latLonObj
        let obj = { lat: lat, lng: lon }

        geocoder.geocode({ 'location': obj , 'language': lang}, function (results, status) {
            //    

            //   ,   catch
            try {
                address = `${results[3].formatted_address}`
            } catch {//formatted_address   ()
                address = (lang==='ko')
                ? "     .   ."
                : 'The address for that location cannot be imported. Please click another location.'
                
            }
            //address = (status === "OK") && results[3].formatted_address
            //  OK,   false   !
            resolve(address)
            // 

        });
    })//promise
}

